import React from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import * as eventActions from '../actions/events';
import Events from '../components/events/Events.jsx';
import Footer from '../components/footer/Footer.jsx';
import Header from '../components/header/Header.jsx';
import Tabs, { TabPane } from 'antd/lib/tabs';
import './App.less';

const App = React.createClass({

  render() {
    const { events, dispatch } = this.props;
    const actions = bindActionCreators(eventActions, dispatch);

    return (<div className="App">
      <Header isFetching={events.isFetching} />
      <Tabs defaultActiveKey="0" size="small">
        <TabPane tab="Events" key="0"><Events events={events} actions={actions} /></TabPane>
        <TabPane tab="Stars" key="1">Stars</TabPane>
      </Tabs>
      <Footer date={events.date} markAllRead={actions.markAllRead} />
    </div>);
  },
});

function mapStateToProps(state) {
  return {
    events: state.events
  };
}

export default connect(mapStateToProps)(App);



